<template>
  <div class="ry-entry">
  </div>
</template>
<script>
import setting from '../../../module/wxParkingNon/setting'
export default {

  name: 'RyEntry',

  data () {
    return {
      module: '',
      moduleMsg: '',
    }
  },

  mounted () {
    this.module = this.$route.params.module
    this.moduleMsg = setting.modules[this.module]

    this.$router.push({
      name: 'list',
      params: {
        state: this.moduleMsg.state,
        ryAppId: this.moduleMsg.ryAppId,
        ryMchId: this.moduleMsg.ryMchId,
      },
    })
  },
  methods: {

  },
}
</script>
