export default {
  name: 'ryParkNo',

  props: {
    parkNo: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },

  data () {
    return {
      parkNoList: ['', '', '', '', '', '', '', ''],
      dataIndex: 0,
      isNew: false,
    }
  },

  watch: {
    parkNo: {
      handler (val, oldVal) {
        this.adjustParkNo(val)
      },
      immediate: true,
    },
  },

  computed: {
    parkNoFormate () {
      return this.parkNoList.join('')
    },
    parkNoLength () {
      return this.isNew ? 8 : 7
    },
  },

  mounted () {
    this.$on('pushValue', (value) => {
      this.pushValueHandler(value)
    })
  },

  methods: {
    editHandler (e) {
      const className = e.target.classList[0]

      if (className === 'ry-parkno-no-el') {
        this.dataIndex = parseInt(e.target.dataset.index, 10)
        this.sendInput()
      } else if (className === 'ry-parkno-no-border') {
        this.showKeyboard = true
        this.sendInput()
      }
    },
    sendInput () {
      if (this.disabled) return
      this.$emit('input', this.dataIndex)
    },
    sendChange () {
      if (this.disabled) return
      this.$nextTick(() => {
        this.$emit('change', this.parkNoFormate)
      })
    },

    adjustParkNo (parkNo) {
      if (parkNo === null || parkNo === undefined) {
        this.parkNoList = Array.from(new Array(8)).map(item => '')
        this.firstInput()

        return
      }

      const _parkNo = String(parkNo)
      this.dataIndex = this.parkNoFormate.length > 0 ? this.dataIndex : _parkNo.length
      this.parkNoList = _parkNo.padEnd(8, ' ').split('')
        .map(item => item === ' ' ? '' : item)
      this.isNew = this.parkNoList.join('').length > 7
      this.sendInput()
    },

    firstInput () {
      this.dataIndex = 0
    },

    pushValueHandler (value) {
      if (value === null || value === undefined || value === '') return

      if (value === 'delete') {
        this.removeCurrentValue()
      } else {
        this.addCurrentVaule(value)
      }
    },
    addCurrentVaule (value) {
      this.parkNoList.splice(this.dataIndex, 1, value)

      if (this.dataIndex < this.parkNoLength - 1) {
        this.addIndex()
      }
      this.sendChange()
    },
    addIndex () {
      this.dataIndex++
      this.sendInput()
    },
    removeCurrentValue () {
      if (this.parkNoList[this.dataIndex] === '') {
        this.minusIndex()
      }

      this.parkNoList.splice(this.dataIndex, 1, '')

      this.sendChange()
      // this.judgeIsNew()
    },
    minusIndex () {
      this.dataIndex = this.dataIndex < 1 ? 0 : --this.dataIndex
      this.sendInput()
    },

    // judgeIsNew () {
    //   if (this.isNew && this.dataIndex !== 7) {
    //     this.isNew = false
    //     this.minusIndex()
    //   }
    // },
    toNewHandler () {
      if (this.dataIndex === this.parkNoLength - 1) {
        this.dataIndex++
        this.sendInput()
      }
      this.isNew = true
    },
  },
}
