<template>
  <div class="ry-add">
    <div class="ry-add-wrap">
      <div class="ry-add-tip"></div>

      <div class="ry-add-no">
        <ry-park-no-compile @change="changeHandler"></ry-park-no-compile>
      </div>

      <div :class="[
        'ry-add-button',
        isDone ? '' : 'ry-add-button--un' ]"
        @click="addHandler"></div>
    </div>

  </div>
</template>
<script>
import RyParkNoCompile from '../ryParkNoCompile/ryParkNoCompile'
export default {

  name: 'RyAdd',

  components: {
    RyParkNoCompile,
  },

  data () {
    return {
      state: '',
      ryAppId: '',
      ryMchId: '',
      openId: '',
      parkNo: '',
    }
  },

  computed: {
    isDone () {
      return this.parkNo && this.parkNo.length >= 7
    },
  },

  mounted () {
    this.state = this.$route.params.state
    this.ryAppId = this.$route.params.ryAppId
    this.ryMchId = this.$route.params.ryMchId
    this.openId = this.$route.params.openId

    // this.getParkNo()
  },
  methods: {

    changeHandler (parkNo) {
      this.parkNo = parkNo
      // this.getParkNo(parkNo)
    },

    addHandler () {
      if (this.isDone) {
        this.getParkNo()
      }
    },

    getParkNo (parkNo) {
      this.$axios.post('/pay-api/afterPay/queryWechatUserStatus', {
        'ryAppId': this.ryAppId,
        'ryMchId': this.ryMchId,
        'openId': this.openId,
        'plateNumber': parkNo || this.parkNo,
      }).then((data) => {
        const list = data.data.result.data

        // this.plateNos = list.plateNos
        if (list.wechatPath) {
          window.location.href = list.wechatPath
        } else {
          this.$router.go(-1)
        }
      })
    },

  },
}
</script>

