import RyParkKeyboard from '../../ryParkKeyboard/ryParkKeyboard'
import RyParkNo from '../ryParkNo/ryParkNo'

export default {

  name: 'ryParkNoCompile',

  components: {
    RyParkKeyboard,
    RyParkNo,
  },

  data () {
    return {
      parkNo: '',
      parkNoChange: '',
      showKeyboard: true,
      borderKey: 'one',
      dataIndex: 1,
      isNew: false,
    }
  },

  computed: {
    // parkNoFormate () {
    //   console.log(this.pakrNoList)

    //   return this.pakrNoList.join('')
    // },
    isShowZh () {
      return this.showKeyboard && this.dataIndex === 0
    },
    isShowOther () {
      return this.showKeyboard && this.dataIndex >= 1
    },
    isShowNu () {
      return this.showKeyboard && this.dataIndex !== 1
    },
    isShowSpecial () {
      // TODO
      return this.showKeyboard && this.dataIndex === 6 && this.parkNoChange.indexOf('Z') === 0
    },
  },

  methods: {
    inputHandler (dataIndex) {
      this.showKeyboard = true
      this.dataIndex = dataIndex
    },
    changeHandler (parkNo) {
      this.parkNoChange = parkNo
      this.$emit('change', parkNo)
    },

    parkNoHandler (value) {
      this.$refs.parkNo.$emit('pushValue', value)
    },
    completeHandler (res) {
      this.showKeyboard = false
      // this.$emit('complete', this.parkNoList && this.parkNoList.join(''))
    },
    deleteHandler (res) {
      // this.parkNoList.pop()
      this.$refs.parkNo.$emit('pushValue', 'delete')
      // this.showKeyboard = false
    },
  },
}
