import {getQueryString} from '../../../public/utils/getQueryString'
import RyParkNoShow from '../ryParkNoShow/ryParkNoShow'
export default {

  name: 'RyList',

  components: {
    RyParkNoShow,
  },

  data () {
    return {
      state: '',
      ryAppId: '',
      ryMchId: '',
      code: '',
      openId: '',
      plateNos: '',
      parkNo: '',
    }
  },

  mounted () {
    this.code = getQueryString('code')
    this.state = this.$route.params.state
    this.ryAppId = this.$route.params.ryAppId
    this.ryMchId = this.$route.params.ryMchId
    this.openId = this.$route.params.openId

    if (this.openId) { // openid
      this.getParkNo()
    } else if (this.code) { // openid
      this.getOpenId()
    } else { // 
      window.location.href = this.getWxOAuthUrl()
    }
  },
  methods: {
    getWxOAuthUrl () {
      const url = [
        'https://open.weixin.qq.com/connect/oauth2/authorize?appid=',
        'wx6adc3ad9a28954bc',
        '&redirect_uri=',
        window.location.href,
        // 'http://wx.fe8216.rongyi.com/h5_lightning/module/wxParkingNon',
        '&response_type=code&scope=snsapi_base&state=1#wechat_redirect',
      ]

      return url.join('')
    },

    completeHandler (parkNo) {
      this.parkNo = parkNo
      // this.getParkNo(parkNo)
    },

    getParkNo (parkNo) {
      this.$axios.post('/pay-api/afterPay/queryWechatUserStatus', {
        // 'ryAppId': 'xn9jiaa6r223',
        'ryAppId': this.ryAppId,
        // 'ryMchId': 'unh4vt31k9',
        'ryMchId': this.ryMchId,
        // 'openId': 'o4GgauEY6lqBQ2Wazp2y1Pq13Ffw', // 
        // eslint-disable-next-line max-len
        // /h5_lightning/module/wxParkingNon/list/1/xn9jiaa6r223/unh4vt31k9/o4GgauEY6lqBQ2Wazp2y1Pq13Ffw
        // 'openId': 'o4GgauJKZJPKYYXZix4y2DVYOVQo', // 
        // eslint-disable-next-line max-len
        // /h5_lightning/module/wxParkingNon/list/1/xn9jiaa6r223/unh4vt31k9/o4GgauJKZJPKYYXZix4y2DVYOVQo
        // 'openId': 'o4GgauMZkjAZ8nc6N8_3xBjxMQT4', // 
        // eslint-disable-next-line max-len
        // /h5_lightning/module/wxParkingNon/list/1/xn9jiaa6r223/unh4vt31k9/o4GgauMZkjAZ8nc6N8_3xBjxMQT4
        // 'openId': 'o4GgauM8d2ixNcjYnTEd30wFkDE0', // 
        // eslint-disable-next-line max-len
        // /h5_lightning/module/wxParkingNon/list/1/xn9jiaa6r223/unh4vt31k9/o4GgauM8d2ixNcjYnTEd30wFkDE0
        openId: this.openId,
        'plateNumber': parkNo || this.parkNo,
      }).then((data) => {
        // eslint-disable-next-line no-console

        const list = data.data.result.data

        const tempNos = list.plateNos || ''
        this.plateNos = tempNos.split(';') || []
        this.plateNos = this.plateNos.map(No => No && No.replace(/(.{2})/, '$1 '))

        if (list.wechatPath) {
          window.location.href = list.wechatPath
        }
      })
    },

    getOpenId (code) {
      this.$axios.post('/pay-api/payAuth/getOpenId', {
        code: this.code,
        state: this.state,
      }).then((data) => {
        this.openId = this.openId || data.data.result.data

        this.getParkNo()
      })
    },

    toAdd () {
      this.$router.push({
        name: 'add',
        params: {
          state: this.state,
          ryAppId: this.ryAppId,
          ryMchId: this.ryMchId,
          openId: this.openId,
        },
      })
    },
  },
}
