import Vue from 'vue'
import Router from 'vue-router'
import RyList from '../../../componments/wxParkingNon/ryList/ryList'
import RyAdd from '../../../componments/wxParkingNon/ryAdd/ryAdd'
import RyEntry from '../../../componments/wxParkingNon/ryEntry/ryEntry'
import config from '../../../../config'
import setting from '../setting'

Vue.use(Router)

// console.log(process.env.RUN_ENV)

export default new Router({
  mode: 'history',
  base: `${config.produceName}/${config.moduleRootName}/${setting.moduleName}`,
  routes: [
    {
      name: 'list',
      path: '/list/:state?/:ryAppId?/:ryMchId?/:openId?',
      component: RyList,
      meta: {title: ''},
    },
    {
      name: 'add',
      path: '/add/:state?/:ryAppId?/:ryMchId?/:openId?',
      component: RyAdd,
      meta: {title: ''},
    },
    {
      name: 'entry',
      path: '/entry/:module',
      component: RyEntry,
      meta: {title: ''},
    },
  ],
})
